@tailwind base;
.brand {
  @apply text-3xl font-medium text-indigo-500;
}

main {
  padding-top: 58px;
  overflow: hidden;
}
@screen md {
  main {
    padding-top: 88px;
  }
}
main .elementor-section.elementor-section-boxed > .elementor-container {
  @apply container mx-auto lg:px-10 xl:px-16;
}

.page-title {
  @apply font-display text-primary text-2xl;
  letter-spacing: 0.2em;
}

.list-style-enable img {
  max-width: 100%;
}
.list-style-enable ul {
  @apply list-disc my-3;
}
.list-style-enable ol {
  @apply list-decimal;
}
.list-style-enable ul li,
.list-style-enable ol li {
  @apply ml-5;
}
.list-style-enable strong {
  @apply font-bold;
}

.text-formatting-enable ul {
  @apply list-disc my-3;
}
.text-formatting-enable ol {
  @apply list-decimal;
}
.text-formatting-enable ul li,
.text-formatting-enable ol li {
  @apply ml-5;
}
.text-formatting-enable a {
  @apply underline font-bold;
}
.text-formatting-enable a:hover {
  @apply text-primary;
}
.text-formatting-enable strong {
  @apply font-bold;
}
.text-formatting-enable p {
  @apply my-3;
}

.slick-next {
  right: 10px !important;
}
@screen xl {
  .slick-next {
    right: 40px !important;
  }
}
@screen lg {
  .slick-next {
    right: 20px !important;
  }
}

.slick-prev {
  left: 10px !important;
  z-index: 10;
}
@screen xl {
  .slick-prev {
    left: 40px !important;
  }
}
@screen lg {
  .slick-prev {
    left: 20px !important;
  }
}

.slick-current {
  z-index: 10;
}

.svg {
  fill: none;
}

@tailwind components;
@tailwind utilities;
.elementor-section-preview {
  @apply py-10 bg-gray-100 flex flex-col justify-center items-center text-center h-96;
}
.elementor-section-preview h1 {
  @apply text-center text-3xl font-bold text-primary;
}
.elementor-section-preview label {
  @apply text-center font-semibold;
  color: red;
}